.sillok 
{
    background: url(../img/bg.jpg);
    background-size: cover;
    .inner
    {
        text-align: center;
        height: 100%;
        h1
        {
            font-size: 26px;
            font-weight: 600;
            line-height: 1.5;
            padding: 20px 0;
        }
        .card-container
        {
            overflow: auto;
            height: 100%;
            transition: .3s;
            :hover
            {
                background: #eee;
            }
            ::-webkit-scrollbar
            {
                display: none;
                width: 0 !important;
            }
            
            .card
            {
                max-width: 480px;
                margin-top: 10px !important;
                margin: 0 auto;
                padding: 10px 0;
                border: 1px solid #333;
                position: relative;
                cursor: pointer;
                ::before
                {
                    content: "";
                    position: absolute;
                    top: -10px;
                    left: -16px;
                    width: 2px;
                    height: 80px;
                    background: #333;
                }
                ::after
                {
                    content: "";
                    position: absolute;
                    top: 4px;
                    left: -21px;
                    width: 12px;
                    height: 12px;
                    background: #333;
                }
                h3 
                {
                    font-size: 12px;
                }
            }
        }
    }
}